.header {
    &__body {
        max-width: 1300px;
        margin-right: auto;
        margin-left: auto;
        padding-left: 30px;
        padding-right: 30px;
        position: relative;
        
    }
    &__container {
        padding-left: 12px;
        padding-right: 12px;
        margin-top: 10px;
        &_active {
            background: #1f1b2e;
            box-shadow: 0 20px 96px #12101b;
            border-radius: 12px 12px 0 0;  
        }
    }
    &__content {
        
        display: flex;
        justify-content: space-between;
        position: relative;
        &::after{
            content: '';
            background-color: rgba(255,255,255,.16);
            height: 1px;
            position: absolute;
            left: 0;
            right: 0;
            bottom: 0;
            pointer-events: none;
        }
    }
    &__block {
        
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
    &__img {
    }
    &__btn-block {
        display: flex;
        justify-content: space-between;
        align-items: center;
        height: 100%;
        &_search {
            margin-left: 8px;
            margin-right: 12px;
            &:hover {
                cursor: pointer;
                .header__svg_search {
                    stroke: white;
                    fill: white;
                    transition-duration: 0.4s;
                }
                .header__text {
                    color: white;
                    transition-duration: 0.4s;
                }
            }
            &:not(:hover):not(:active) {
                .header__svg_search {
                    stroke: gray;
                    fill: gray;
                    transition-duration: 0.4s;
                }
                .header__text {
                    color: gray;
                    transition-duration: 0.4s;
                }
            }
        }
        &_notification{
            padding-right: 12px;
            &:hover {
                cursor: pointer;
                .header__svg_notification {
                    stroke: white;
                    fill: white;
                    transition-duration: 0.4s;
                }
            }
            &:not(:hover):not(:active) {
                .header__svg_notification {
                    stroke: gray;
                    fill: gray;
                    transition-duration: 0.4s;
                }
            }
        }
        &_login {
            padding: 12px;

            border-radius: 8px;
            border: 2px solid rgba(255,255,255,.48);
            background-color: transparent;
            &:hover {
                border-color: white;
                transition-duration: 0.4s;
                cursor: pointer;
                .header__svg_login {
                    stroke: white;
                    fill: white;
                    transition-duration: 0.4s;
                }
            }
            &:not(:hover):not(:active) {
                border-color: gray;
                transition-duration: 0.4s;
                .header__svg_login {
                    stroke: gray;
                    fill: gray;
                    transition-duration: 0.4s;
                }
            }
        }  
    }
    &__svg {
        &_search {
            margin-right: 8px;
        }
    }
    &__text {
        font-size: 15px;
        line-height: 20px;
        font-weight: 500;
        color: rgba(255,255,255,.48);
    }
    
    &__headerMenu {
        margin-left: 32px;
    }
    &__dropDownBody {
        margin-right: auto;
        margin-left: auto;

        //Исправить z-index, согласно Filters
        z-index: 1999999999;
        display: block;

        //min-height: 344px;
        
        background: #1f1b2e;
        box-shadow: 0 20px 96px #12101b;
        border-top: none;
        border-radius: 0 0 12px 12px;
        
        position: absolute;
        &_hidden {
            display: none;
            z-index: -2;
        }
    }
}

.headerMenu {
    &__navigation {
    }
    &__list {
        display: flex;
        justify-content: space-between;
    }
    &__listItem {
        &:first-child {
            padding-left: 0;
        }
    }
    &__link {
        padding: 10px;
        
    }
    &__text {
        transform: translateY(6px);
        padding-left: 16px;
        padding-bottom: 14px;
        height: 100%;

        font-size: 15px;
        line-height: 20px;
        font-weight: 500;
        color: rgba(255,255,255,.48);

        transition-property: color;
        &:hover {
            color: white;
            transition-duration: 0.4s;
        }
        &:not(:hover):not(:active) {
            color: rgba(255,255,255,.48);
            transition-duration: 0.4s;
        }
    }
}

.dropDownBody{
    &__container{
        padding-left: 12px;
        padding-right: 12px;
    }
    &__films {
        padding: 10px 28px 20px;
        display: flex;
        justify-content: space-between;
        height: 370px;
        &_hidden {
            display: none;
        }
    }
    &__series {
        padding: 10px 28px 20px;
        display: flex;
        justify-content: space-between;
        height: 370px;
        &_hidden {
            display: none;
        }
    }
    &__mults {
        padding: 10px 28px 20px;
        display: flex;
        justify-content: space-between;
        height: 370px;
        &_hidden {
            display: none;
        }
    }
    &__TV {
        padding: 10px 28px 20px;
        display: flex;
        justify-content: space-between;
        height: 370px;
        &_hidden {
            display: none;
        }
    }
    //Внутри
    &__block {
        min-width: 180px;
        //margin-right: 20px;
        display: flex;
        justify-content: space-between;
        &_big {
            //flex-grow: 1;
        }
        &_subscribe{
            margin-left: 20px;
            flex-direction: column;
        }
        &_tv {
            flex-direction: column;
        }
    }
    &__column {
        min-width: 180px;
        //margin-right: 20px;
    }
    &__list-head {
        font-weight: 500;
        margin-top: 20px;
        color: white;
    }
    &__list {
        display: flex;
    }
    &__list-column {
        min-width: 180px;
        
        //margin-right: 20px;
    }
    &__list-link {
        
    }
    &__list-item {
        margin-top: 8px;
        width: 200px;
        line-height: 18px;
        color: gray;
        &:hover {
            color: white;
        }
    }
    &__nav-block {
        margin-top: 10px;
    }
    &__nav-list {
       border-left: 1px solid gray; 
       height: 100%;
    }
    &__nav-list-item {
        padding-left: 10px;
        padding-top: 4px;
        padding-bottom: 4px;
        line-height: 20px;
        color: gray;
        &_active {
            color: white;
            border-left: 1px solid white;    
        }
        
    }
    &__adv{
        height: 260px;
        border-radius: 10px;
        background-color: #2b2640
    }
}